export default {
	data() {
		return {
			showD:false,
			showP:false,
			isactive1: false,
			isactive2: false,
		}
	},
	onLoad() {
		this.clickD()
	},
	methods: {
		clickD() {
			this.showD = true
			this.showP = false
			this.isactive1 = true
			this.isactive2 = false
		},
		clickP() {
			this.showD = false
			this.showP = true
			this.isactive1 = false
			this.isactive2 = true
		}
		
	}
}
